* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  list-style: none;
  padding: 0;
}

img {
  display: inline-block;
  margin-right: 8px;
}

.country-info__name {
  font-size: 27px;
  font-weight: 700;
}

.country-list__name {
  font-weight: 600;
  font-size: 18px;
}

.country-info__item {
  font-weight: 600;
  font-size: 21px;
}

.country-info__item span {
  font-weight: 400;
  font-size: 18px;
}

.country-list__item {
  display: flex;
  align-items: center;
}

.country-info__head {
  display: flex;
  align-items: center;
}
